import React, { useState, useEffect } from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";

import {
    OptionImage,
    QuestionText,
    QuizContainer,
    ResultItem,
    Results,
} from "./styled";
import { getQuestions, Question } from "./api";
import Spinner from "components/common/spinner";
import Tick from "./images/tick";
import Cross from "./images/cross";
import { randomizeQuestions } from "./utils";
import { GetStartedButton } from "components/homepage/styled";

interface Answered {
    questionId: number;
    answerId: number;
    isCorrect: boolean;
}

const Quiz = () => {
    const [questions, setQuestions] = useState<Question[]>([]);
    const [questionIndex, setQuestionIndex] = useState<number>(0);
    const [answeredQuestions, setAnsweredQuestions] = useState<Answered[]>([]);
    const [ticked, setTicked] = useState<number | null>(null);
    const [crossed, setCrossed] = useState<number | null>(null);

    const question = questions[questionIndex];

    useEffect(() => {
        getQuestions().then((result) => setQuestions(result));
    }, []);

    const nextQuestion = (answerId: number) => {
        if (!question || ticked !== null || crossed !== null) return;
        const correctAnswer = question.answers.find(
            (answer) => answer.isCorrect
        );
        const isCorrect = correctAnswer ? correctAnswer.id === answerId : false;
        const answered = {
            questionId: question.id,
            answerId,
            isCorrect,
        };
        setAnsweredQuestions([
            answered,
            ...answeredQuestions.filter(
                (answer) => answer.questionId !== question.id
            ),
        ]);
        setTimeout(() => {
            setQuestionIndex(questionIndex + 1);
            setTicked(null);
            setCrossed(null);
        }, 2000);
        isCorrect ? setTicked(answerId) : setCrossed(answerId);
    };

    const resetQuiz = () => {
        setQuestions(randomizeQuestions(questions));
        setQuestionIndex(0);
        setAnsweredQuestions([]);
    };

    let content;

    if (questions.length > 0 && questionIndex === questions.length) {
        content = (
            <Results>
                <ResultItem>
                    Total correct:{" "}
                    {
                        answeredQuestions.filter(
                            (answered) => answered.isCorrect
                        ).length
                    }
                </ResultItem>
                <ResultItem>
                    <GetStartedButton size="lg" variant="outline-primary">
                        Sign up to practice more.
                    </GetStartedButton>
                </ResultItem>
                <ResultItem>
                    <Button onClick={resetQuiz} variant="link">
                        Try again
                    </Button>
                </ResultItem>
            </Results>
        );
    } else if (!question) {
        content = (
            <Row>
                <Spinner />
            </Row>
        );
    } else {
        content = (
            <React.Fragment>
                <Row>
                    <QuestionText>{question.question}</QuestionText>
                </Row>
                <Row>
                    {question.answers.map((answer) => {
                        const isTicked = answer.id === ticked;
                        const isCrossed = answer.id === crossed;
                        return (
                            <Col lg="6" key={`${answer.id}`}>
                                <OptionImage
                                    isTransparent={isTicked || isCrossed}
                                    src={answer.img}
                                    onClick={() => nextQuestion(answer.id)}
                                />
                                {isTicked && <Tick />}
                                {isCrossed && <Cross />}
                            </Col>
                        );
                    })}
                </Row>
            </React.Fragment>
        );
    }
    return <QuizContainer>{content}</QuizContainer>;
};

export default Quiz;
